<template>
  <div v-if="datalist">
    <div class="header">
      <p @click="skip('/city')">{{cityName}}</p>
      <h3>影院</h3>
      <span class="iconfont iconsousuo" @click="skip('/search')"></span>
    </div>
    <div class="nav">
      <p @click="isArea=!isArea" :class="{oCity:isArea}">{{changeArea}}</p>
      <p>APP订票</p>
      <p>最近去过</p>
    </div>

    <!-- 区域 起始 -->
    <div class="oArea" v-if="isArea">
      <ul>
        <li v-for="(area,index) in oArea" :key="index" @click="oChange(area)" :class="changeArea===area?'active':''">{{area}}</li>
      </ul>
    </div>
    <!-- 区域 结尾 -->

    <!-- cinemalist start -->
    <div class="cinemalist">
      <ul>
        <li v-for="data in checkArea" :key="data.cinemaId" @click="skip('/CinemaDetils/'+data.cinemaId)">
          <p>{{data.name}}</p>
          <span>{{data.address}}</span>
        </li>
      </ul>
    </div>
    <!-- cinemalist end -->

  </div>
</template>

<script>
import {mapActions,mapState} from 'vuex'
export default {
  data(){
    return{
      // datalist:null,
      isArea:false,
      changeArea:'全城'
    }
  },
  methods:{
    ...mapActions("cinema",["getCinema"]),
    oChange(data){
      this.changeArea = data,
      this.isArea = false
    },
    skip(path){
      this.$router.push(path)
    }
  },
  computed:{
    ...mapState("cinema",["datalist"]),
    ...mapState("city",["cityName","cityId"]),
    oArea(){
      let newArr = this.datalist.map(item=>item.districtName)
      let areaArr = ['全城',...Array.from(new Set(newArr))]
      return areaArr
    },
    checkArea(){
      if(this.changeArea === '全城'){
        return this.datalist
      }else{
        return this.datalist.filter(item=>item.districtName===this.changeArea)
      }
    }
  },
  created(){
    this.getCinema(this.cityId)
    // instance('/gateway?cityId=310100&ticketFlag=1&k=3622282',{
    //   headers:{
    //     'X-Host': 'mall.film-ticket.cinema.list'
    //   }
    // }).then(res=>{
    //   console.log(res.data.data.cinemas)
    //   this.datalist = res.data.data.cinemas
    // })
  }

}

</script>
<style lang="scss" scoped>
.oArea{
  position: fixed;
  top: .8rem;
  left: 0;
  width: 100%;
  background: white;
  padding: 10px 0;
  ul{
    display: flex;
    justify-content: space-around;
    width: 100%;
    flex-flow: wrap;
    li{
      margin-bottom: 5px;
      width: 81px;
      height: 30px;
      border: 1px solid lightgray;
      text-align: center;
      line-height: 30px;
      &.active{
        border: 1px orange solid;
        color: orange;
      }
    }
  }
}
.cinemalist{
  margin-top: .8rem;
  width: 100%;
  ul{
    width: 100%;
    padding: 0 15px;
    li{
      margin-bottom: 15px;
      p{
        font-size: 16px;
        color: black;
      }
      span{
        color: wheat;
      }
    }
  }
}
.header{
  background: white;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: .4rem;
  width: 100%;
  padding: 0 .1rem;
}
.nav{
  background: white;
  position: fixed;
  top: .4rem;
  left: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: .4rem;
  width: 100%;
  padding: 0 .1rem;
  .oCity{
    color: orange;
  }
  p{
    position: relative;
    left: .15rem;
  }
}
</style>